import React, { Component } from 'react';
import './Home.css';
import BackgroundVideo from './../../assets/video/zoomtocoorg.mp4';
import coffeeLove from './../../assets/img/coffee-bean-heart_transparent.png';

export class Home extends Component {
  state = {
    videoEnded: false,
    videoLoading: true,
    animEnded: false,
  };

  componentDidMount() {
    if (this.video) {
      this.video.addEventListener('loadeddata', () => {
        this.setState({ videoLoading: false });
      });
    }
  }

  componentWillUnmount() {
    if (this.video) {
      this.video.removeEventListener('loadeddata', () => {});
    }
  }

  fadeVideo() {
    this.setState(
      {
        videoEnded: true,
      },
      () => {
        setTimeout(() => {
          this.setState({
            animEnded: true,
          });
        }, 1500);
      }
    );
  }

  render() {
    return (
      <div className="home-container">
        {this.state.animEnded ? (
          <div className="afterEnded">
            <div className="afterEnded-image">
              <img
                width="60%"
                src={coffeeLove}
                alt="coffee-hearts"
                className="afterEnded-image__image"
              />
            </div>

            <div className="afterEnded-text">
              <div>
                <span>Caf√©</span> <span>amor</span>
              </div>
              <div className="afterEnded-text-subtitle">
                Home of Coffee - Coorg, India
              </div>
              <div className="afterEnded-text-subtitle italic">Estd. 1929</div>
            </div>
          </div>
        ) : (
          <div className="video-container">
            <video
              ref={(ref) => (this.video = ref)}
              onEnded={() => this.fadeVideo()}
              className="videoTag"
              autoPlay
              muted
              style={{
                opacity: this.state.videoLoading
                  ? 0
                  : this.state.videoEnded
                  ? 0
                  : 1,
                transition: 'opacity, 1.5s ease-in-out',
              }}
            >
              <source src={BackgroundVideo} type="video/mp4" />
            </video>
          </div>
        )}
      </div>
    );
  }
}

export default Home;
